body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top: 80px;
}


.App-link {
  color: #61dafb;
}

.guessLetter {
  padding-bottom: 50px;
}

.errors {
  color: #ff0000;
  font-size: 20px;
  margin-top: 100px;
  font-weight: bold;
}

.gameStart {
  padding-bottom: 50px;
}

.letterHolder {
    display: inline;
    margin-right: 10px;
    border-bottom: 3px solid #ccc;
    font-size: 25px;
    padding: 0 10px;
}

